.search {
	position: relative;
	&__field {
		position: relative;
		padding-left: 50px;
		&:before {
			content: '';
			position: absolute;
			left: 10px;
			right: 0;
			bottom: 2px;
			border-bottom: 2px solid $white;
			opacity: 0.7; } }
	&__input {
		padding: 0 10px;
		width: 100%;
		height: 50px;
		color: $white;
		background: none;
		@include rreg;
		font-size: 16px;
		line-height: normal;
		&::placeholder {
			color: $white;
			opacity: .7; } }
	&__controls {
		position: absolute;
		top: 0;
		left: 0;
		padding: 5px;
		font-size: 0; }
	&__button {
		display: inline-block;
		vertical-align: top;
		color: $white; } }

.search-dropdown {
	position: absolute;
	z-index: 100;
	top: 100%;
	left: 10px;
	right: 0;
	margin-top: 14px;
	opacity: 0;
	visibility: hidden;
	box-shadow: 0 0 4px 2px rgba($black, 0.15);
	transition: opacity 0.15s ease-in-out, visibility 0.15s ease-in-out;
	&.is-open {
		opacity: 1;
		visibility: visible; }
	&__container {
		position: relative;
		z-index: 2;
		background: $white; }
	&__body {
		overflow: auto;
		max-height: 199px; }
	&__list {
		padding: 0 12px; }
	&__item {
		padding: 10px 0;
		margin: 0 -5px;
		color: $darkGray;
		font-size: 16px;
		cursor: pointer;
		transition: color 0.17s ease-in-out;
		@include clr;
		&:not(:last-child) {
			border-bottom: 1px solid $lightGrayActive; }
		@include hover {
			color: $blue; } }
	&__price,
	&__text {
		padding: 0 5px; }
	&__price {
		float: right;
		@include rreg;
		white-space: nowrap; }
	&__text {
		overflow: hidden;
		@include rlight; }
	&__arrow {
		position: absolute;
		z-index: 1;
		left: 40px;
		bottom: 100%;
		width: 20px;
		height: 20px;
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 14px;
			height: 14px;
			background: $white;
			box-shadow: 0 0 4px 2px rgba($black, 0.15);
			transform: translate3d(-50%,50%,0) rotate(45deg); } } }
