.printer-setup {
	&__title {
		padding-bottom: 10px;
		@include rmed;
		font-size: 20px; }
	&__row {
		padding: 10px 0;
		font-size: 0;
		@include nosel;
		@include clr; }
	&__checkbox,
	&__center,
	&__test {
		display: inline-block;
		vertical-align: middle;
		padding-right: 15px;
		font-size: 15px;
		line-height: 1.3; }
	&__checkbox {
		position: relative;
		padding-left: 42px;
		width: 50%;
		min-height: 30px;
		line-height: 30px;
		.checkbox {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translate3d(0,-50%,0); }
		label {
			line-height: 1.3; } }
	&__center,
	&__test {
		width: 25%; }
	label {
		display: inline-block;
		width: 100%;
		cursor: pointer; }
	&__radios {
		padding: 5px 0 0 42px; }
	&__radio {
		padding: 10px 0; } }
