.sm-box {
	position: relative;
	padding: 10px;
	border-radius: 2px;
	color: $darkGray;
	background: $lightGray;
	box-shadow: 0 0px 1px rgba($black, 0.2), 0 2px 4px rgba($black, 0.1);
	text-align: left;
	cursor: pointer;
	@include nosel;
	transition: box-shadow 0.17s ease-in-out, background 0.17s ease-in-out, color 0.17s ease-in-out;
	@include hover {
		box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); }
	@include active {
		box-shadow: none; }
	&.is-active {
		color: $white;
		background: $blue !important;
		.sm-box {
			&__icon {
				opacity: 1; }
			&__title {
				padding-right: 25px; } } }
	&__title,
	&__description {
		@include text-overflow; }
	&__title {
		font-size: 20px; }
	&__description {
		opacity: 0.5;
		font-size: 13px; }
	&__icon {
		position: absolute;
		top: 0;
		right: 0;
		opacity: 0;
		transition: opacity 0.17s ease-in-out; } }
