.field-box {
	position: relative;
	padding: 13px 20px;
	border: 2px solid $lightGray;
	background: $white;

	//mods
	&_switch {
		padding: 0;
		min-height: 60px;
		.field-box {
			&__label {
				padding: 22px 80px 22px 20px;
				cursor: pointer; } } }

	//elements
	&__content {
		margin: 0 -10px;
		@include clr; }
	&__col {
		float: left;
		padding: 0 10px;
		width: 100%;
		&_sm {
			width: 33.33%; }
		&_md {
			width: 50%; } }

	//switcher
	&__switch {
		position: relative;
		margin: -13px -20px;
		min-height: 60px;
		.field-box {
			&__label {
				padding: 22px 80px 22px 20px;
				cursor: pointer; } } }
	&__label {
		display: block;
		padding: 5px 10px 5px 0;
		font-size: 14px;
		@include nosel; }
	&__switcher {
		position: absolute;
		top: 15px;
		right: 18px; }

	//radio/checkboxes
	&__list {
		padding: 0 10px;
		margin: 0 -20px;
		@include clr; }
	&__radio,
	&__checkbox {
		display: block;
		padding: 5px 10px; }

	//counter
	&__counter {
		margin: -13px -20px;
		font-size: 0;
		.field-box {
			&__label {
				display: inline-block;
				vertical-align: middle;
				padding-left: 20px;
				padding-right: 20px;
				width: 35%; } }
		&-input {
			width: 80px;
			// height: 80px
			// font-size: 30px
			height: 60px;
			font-size: 20px;
			// +hsm
			// 	height: 60px
 } }			// 	font-size: 20px
	&__controls {
		display: inline-block;
		vertical-align: middle;
		width: 65%;
		@include clr;
		&-in {
			position: relative;
			float: right;
			width: 240px;
			padding: 0 80px;
			max-width: 100%; } }
	&__control {
		position: absolute;
		top: 0;
		bottom: 0;
		&_left {
			left: 0; }
		&_right {
			right: 0; }
		.btn {
			height: 100%;
			line-height: normal; } }
	//select-set
	&__select-set {
		position: relative;
		// padding: 0 90px 0 35px
		padding-right: 90px;
		margin: -13px -20px;
		min-height: 60px;
		@include clr; }
	&__select-col {
		float: left;
		width: 50%;
		@include clr; }
	&__select-input {
		padding: 0 10px;
		text-align: right; }
	&__select-static {
		padding-left: 20px;
		i {
			float: right;
			margin: 10px 0;
			color: $green; }
		span {
			display: block;
			overflow: hidden;
			// +rreg
			// font-size: 20px
			@include rlight;
			font-size: 14px;
			line-height: 60px;
			@include nosel; } }
	&__number {
		position: absolute;
		top: 0;
		left: 20px;
		width: 15px;
		font-size: 14px;
		line-height: 60px;
		@include nosel; }
	&__button {
		position: absolute;
		top: -2px;
		right: -2px;
		bottom: -2px;
		width: 90px;
		.btn {
			padding: 0;
			height: 100%;
			line-height: normal; } }
	&__clear {
		position: absolute;
		top: 0;
		left: 100%;
		bottom: 0;
		width: 60px;
		color: $red;
		background: none; }
	.dropdown {
		&__value {
			font-size: 14px; } } }
