.form-box {
	border: 2px solid $lightGray;
	&.is-focus {
		border-color: $blue; }
	&__field + &__field {
		margin-top: 1px; }
	&__field {
		position: relative;
		min-height: 54px;
		background: $white; }
	&__label {
		display: block;
		padding: 21px 80px 21px 20px;
		font-size: 14px;
		line-height: 1;
		@include nosel;
		cursor: pointer; }
	&__switcher {
		position: absolute;
		top: 13px;
		right: 20px; }
	&__payment {
		padding: 18px 20px;
		background: $white;
		@include clr;
		li {
			float: left;
			width: 50%; }
		li + li {
			text-align: right; } } }
