.main-help {
	position: relative;
	height: 100%;
	&__slider,
	&__slide,
	.slick-list,
	.slick-track {
		height: 100%; }
	&__close {
		position: absolute;
		z-index: 50;
		top: 0;
		right: 0;
		color: $red;
		background: none; }
	&__slide {
		padding: 50px 0 60px;
		font-size: 0;
		text-align: center;
		&:before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			width: 0;
			height: 100%; }
		img {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; } }
	&__controls {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		overflow: hidden;
		background: $blue;
		font-size: 0; }
	&__control {
		position: relative;
		z-index: 5;
		display: inline-block;
		vertical-align: top;
		border-top: 1px solid $blue;
		border-right: 1px solid $alabaster;
		width: 25%;
		color: $white;
		text-align: center;
		cursor: pointer;
		@include nosel;
		transition: background 0.175s ease;
		&.is-active {
			z-index: 10;
			border-right: none;
			color: $blue;
			background: $alabaster;
			pointer-events: none;
			@include active {
				background: $alabaster; }
			&:before {
				opacity: 1; } }
		@include active {
			background: $blueActive; }
		&:before {
			content: '';
			position: absolute;
			z-index: 1;
			top: 50%;
			left: 100%;
			margin: -8px 0 0 -1px;
			@include arr(10, 16, $alabaster, r);
			opacity: 0;
			transition: opacity 0.175s ease; }
		span {
			display: block;
			@include rreg;
			font-size: 16px;
			line-height: 59px; } } }
