$poyntContainerIndex: 30;
$poyntSidebarIndex: 35;
$poyntFullinfoIndex: 55;
$menuButtonIndex: 50;

.main-header {
	position: absolute;
	z-index: 20;
	top: 0;
	left: 0;
	right: 0;
	color: $white;
	background: $blue;
	@include poynt {
		z-index: initial; }
	&_simple-search {
		.main-header {
			&__title {
				@include poynt {
					padding-right: 300px;
					padding-left: 130px; } } } }

	//containers
	&__sidebar {
		position: relative;
		z-index: 20;
		float: left;
		width: 40%;
		box-shadow: -3px -5px 4px 5px rgba(0, 0, 0, 0.2);
		@include poynt {
			z-index: $poyntSidebarIndex;
			color: $darkGray;
			background: $alabaster;
			box-shadow: none; }
		.main-header {
			&__toolbar {
				@include poynt {
					padding-right: 58px; } } }
		.dropdown_header {
			@include poynt {
				width: 100%; }
			.dropdown__arrow {
				@include poynt {
					color: $darkGray; } } }
		.dropdown-menu {
			@include poynt {
				left: auto;
				right: 0; } } }

	&__container {
		position: relative;
		float: right;
		@include poynt {
			z-index: $poyntContainerIndex; }
		.main-header {
			&__searchbox {
				@include poynt {
					padding-left: 60px;
					padding-right: 188px;
					max-width: none; } } }
		.search {
			&__field {
				@include poynt {
					padding-left: 60px; }
				&:before {
					@include poynt {
						left: 15px;
						right: 5px; } } }
			&__controls {
				@include poynt {
					left: 10px; } } } }
	&__fullinfo {
		position: absolute;
		z-index: 15;
		top: 0;
		right: 0;
		bottom: 0;
		box-shadow: -3px -5px 4px 5px rgba(0, 0, 0, 0.2);
		transform: translate3d(-110%,0,0);
		transition: transform 0.3s ease-in-out;
		@include poynt {
			z-index: $poyntFullinfoIndex;
			bottom: initial;
			height: 50px; }
		&.is-open {
			@include gpu; } }
	&__sidebar,
	&__container,
	&__fullinfo {
		min-height: 50px;
		background: $blue;
		@include poynt {
			float: none;
			width: 100%; } }
	&__container,
	&__fullinfo {
		width: 60%;
		&_full {
			width: 100%;
			.main-header {
				&__title {
					text-align: center; }
				&__searchbox {
					@include poynt {
						padding-left: 0;
						padding-right: 0;
						width: 300px; } } } } }
	//elements
	&__menu {
		position: absolute;
		z-index: $menuButtonIndex;
		top: 0;
		left: 0;
		width: 60px;
		height: 50px;
		color: $blue;
		background: #cae9f4;
		font-size: 32px;
		line-height: 50px;
		transition: color 0.175s ease, background 0.175s ease;
		@include active {
			color: $white;
			background: darken(#cae9f4, 10%); }
		@include touch {
			width: 80px; }
		@include poynt {
			position: fixed;
			top: 0;
			left: 0;
			z-index: ($poyntContainerIndex + 1); }
		~ .main-header__toolbar {
			padding-left: 60px;
			@include touch {
				padding-left: 80px; }
			@include poynt {
				padding-left: 0; } } }
	&__help,
	&__delete {
		position: absolute;
		top: 5px;
		right: 5px;
		color: $white;
		@include poynt {
			right: 135px; } }
	&__delete {
		@include poynt {
			color: $darkGray; }
		+ .main-header__toolbar {
			padding-right: 50px; } }
	&__sync {
		position: absolute;
		z-index: 10;
		top: 0;
		right: 0;
		height: 50px;
		padding: 5px 13px 5px 5px;
		@include poynt {
			z-index: ($poyntContainerIndex + 1); }
		.btn-sync {
			margin: -5px; }
		~ .main-header {
			&__sidebar {
				@include poynt {
					padding-right: 180px; }
				.main-header__toolbar {
					@include poynt {
						padding-right: 0; } } } } }
	&__indicator {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		width: 8px;
		background: $red;
		transition: background 0.15s ease-in-out;
		&[data-state="active"] {
			background: $green; } }
	&__search-for {
		padding: 0 0 0 10px;
		margin: -5px -5px -5px 0;
		height: 50px;
		color: inherit;
		background: none;
		@include rlight;
		font-size: 16px;
		@include poynt {
			font-size: 14px; }
		@include active {
			color: inherit;
			background: rgba(0, 0, 0, 0.1);
			box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2); }
		span, .icon {
			display: inline-block;
			vertical-align: middle; }
		span {
			@include poynt {
				margin-right: -5px; } } }
	&__scan {
		position: absolute;
		z-index: $poyntSidebarIndex;
		right: 0;
		bottom: 0;
		display: none;
		@include poynt {
			display: block;
			border-left: 1px solid rgba($black, 0.2); }
		.btn-scan {
			color: $darkGray; } }

	&__searchbox {
		max-width: 400px;
		&_right {
			position: absolute;
			top: 0;
			right: 50px;
			width: 30%;
			max-width: none;
			@include poynt {
				right: 0; } } }
	&__toolbar {
		.toolbar {
			&__col {
				border: none;
				@include poynt {
					border-right: 1px solid rgba($black, 0.2); } } } }
	&__title {
		padding-top: 13px;
		padding-bottom: 13px;
		margin: 0;
		font-size: 20px; }
	&__back {
		position: absolute;
		z-index: 10;
		top: 0;
		left: 0;
		white-space: nowrap;
		@include poynt {
			z-index: $poyntSidebarIndex; }
		.main-header__title {
			display: inline-block;
			vertical-align: middle; } }
	.is-hidden {
		display: none; } }
