	// 		+poynt
	// 			width: 0
	// 			height: auto
	// 			min-height: 0
	// 	&__container
	// 		z-index: 20
	// 		float: none
	// 		margin-left: 300px
	// 		width: auto
	// 		box-shadow: 3px -5px 4px 5px rgba(0, 0, 0, 0.2)
	// 		+poynt
	// 			margin-left: 0
	// 			text-align: center
	&__sidebar,
	&__container {
		position: relative;
		padding-top: 50px;
		height: 100%; }
	&__sidebar {
		float: left;
		border-right: 2px solid rgba($black, 0.1);
		width: 300px;
		@include poynt {
			z-index: 5;
			border-right: none;
			width: 100%;
			height: auto;
			font-size: 0; }
		&.is-dropdown-upper {
			@include poynt {
				z-index: 50; } }
		.infoblock {
			@include poynt {
				display: inline-block;
				vertical-align: top;
				width: 50%; }
			&__col {
				@include poynt {
					padding: 1px 20px; } } }
		hr {
			@include poynt {
				display: none; } } }
	&__container {
		margin-left: 300px;
		// box-shadow: 3px 0 4px 5px rgba(0, 0, 0, 0.2)
		@include poynt {
			padding-top: 400px;
			margin-left: 0; } }
	&__info {
		overflow: auto;
		height: 100%; }
	&__content,
	&__body {
		height: 100%; }
	&__body {
		// padding-bottom: 210px
		padding-bottom: 100px; }
	&__footer {
		// height: 210px
		box-shadow: inset 0 6px 4px -5px rgba(0, 0, 0, 0.2); }
	&__buttons {
		position: relative;
		padding: 20px;
		font-size: 0;
		text-align: center;
		.btn {
			padding-left: 20px;
			padding-right: 20px;
			margin: 0 4px; } }
	&__done {
		margin: 0 auto;
		max-width: 300px;
		.btn {
			margin: 0; }
		@include xlg {
			float: right;
			width: 100%; } }
	&__showmore {
		// position: absolute
		// top: 30px
		// left: 20px
		float: left;
		.btn {
			margin: 0 20px 0 0;
			min-width: 80px; } }
	&__action {
		font-size: 0; } }

.order-totals {
	height: 110px;
	@include clr;
	&__table {
		float: right;
		padding-right: 7%;
		width: 41%; }
	&__tr {
		font-size: 0;
		text-align: right;
		&:not(:first-child) {
			border-top: 1px solid $lightGray; } }
	&__td {
		display: inline-block;
		vertical-align: middle;
		padding: 11px 10px;
		width: 50%;
		font-size: 14px;
		line-height: 1; }
	&__label {
		opacity: 0.5; }
	&__value {
		em {
			@include rbold; } } }

.order-notes {
	position: relative;
	float: left;
	// padding: 20px
	// width: 59%
	// height: 100%
	font-size: 14px;
	&__icon {
		position: absolute;
		// top: 10px
		// left: 10px
		top: -10px;
		left: -10px; }
	&__content {
		// overflow: auto
		padding-left: 30px;
		margin: 2px 0;
 } }		// height: 64px
