.default-pagination {
  font-size: 0;
  text-align: center;
  &__list {
    padding: 6px 0; }
  &__item,
  &__btn {
    display: inline-block;
    vertical-align: top; }
  &__item {
    padding: 3px; }
  &__btn {
    border-radius: 50%;
    width: 12px;
    height: 12px;
    background: $lightGray;
    transition: box-shadow 0.17s ease-in-out;
    @include hover {
      box-shadow: 0px 1px 2px rgba($black, 0.2); }
    @include active {
      box-shadow: inset 1px 1px 2px rgba($black, 0.3); }
    &.is-active {
      background: $blue; } } }

