.footnote a:active { color: #2183bf; }

.no-touch .footnote a:active { color: #2183bf; }

.loader { position: absolute; z-index: 3000; top: 0; left: 0; right: 0; bottom: 0; background: rgba(255, 255, 255, 0.9); text-align: center; font-size: 0; }

.loader:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

