.step-body { position: relative; padding-bottom: 100px; }

.step-body__section { overflow: auto; padding: 20px 0 0; max-height: 100%; }

.step-body__footer { position: absolute; left: 0; right: 0; bottom: 0; }

.explanation__icon { float: left; }

.explanation__text { overflow: hidden; padding: 10px 0; }

.explanation__text p { margin: 0; }

.explanation__error { clear: both; }

.signature-box { position: relative; margin-bottom: 20px; border-top: 1px solid #dadada; border-bottom: 1px solid #dadada; min-height: 250px; min-height: 300px; min-height: calc(230px + (300 - 230) * ((100vh - 570px) / (650 - 570))); }

@media (max-height: 570px) { .signature-box { min-height: 230px; } }

@media (min-height: 650px) { .signature-box { min-height: 300px; } }

.is-poynt .signature-box { min-height: 450px; }

.signature-box__close { position: absolute; top: 10px; right: 10px; }

.vertical-center { text-align: center; font-size: 0; }

