.header {
	position: relative;

	height: 50px;

	color: $white;
	background: $blue;

	@include rmed;
	font-size: 20px;

	cursor: default;
	@include nosel;

	//mods
	&_large {
		height: 150px; }
	&_medium {
		height: 90px; }
	&_double {
		position: absolute;
		z-index: 20;
		top: 0;
		left: 0;
		right: 0;
		@include poynt {
			z-index: initial; } }
	&_container-upper {
		.header {
			&__sidebar {
				z-index: initial;
				box-shadow: none; }
			&__container {
				z-index: 10;
				box-shadow: 3px -5px 4px 5px rgba(0, 0, 0, 0.2); } } }
	&_settings {
		.header {
			&__sidebar {
				width: 300px; }
			&__container {
				float: none;
				margin-left: 300px;
				width: auto;
				@include poynt {
					margin: 0; } } } }
	&_poynt-sm {
		.header {
			&__sidebar {
				@include poynt {
					float: left;
					width: auto; } }
			&__container {
				@include poynt {
					overflow: hidden;
					width: auto;
					text-align: center; } } } }
	&_payments,
	&_order {
		.header {
			&__back {
				display: none;
				@include poynt {
					display: block; } }
			&__title {
				@include poynt {
					text-align: center; } } } }
	//components double
	&__sidebar {
		z-index: 20;
		float: left;
		width: 40%;
		box-shadow: -3px -5px 4px 5px rgba(0, 0, 0, 0.2);
		@include poynt {
			z-index: 35; }
		.header {
			&__delete {
				@include poynt {
					display: none; } }
			&__toolbar {
				@include poynt {
					padding-right: 58px; } } } }
	&__container {
		float: right;
		@include poynt {
			z-index: 30; }
		.header {
			&__searchbox {
				@include poynt {
					padding-right: 188px;
					max-width: none;
					background: #e6e6e6; } }
			&__delete {
				right: 8px;
				display: none;
				color: $darkGray;
				@include active {
					color: $darkGray; }
				@include poynt {
					display: block; } } }
		.search {
			&__field {
				@include poynt {
					padding-left: 60px; }
				&:before {
					@include poynt {
						left: 15px;
						right: 5px;
						border-color: $darkGray; } } }
			&__controls {
				@include poynt {
					left: 10px; } }
			&__input,
			&__button {
				@include poynt {
					color: $darkGray; } }
			&__input {
				@include placeholder {
					@include poynt {
						color: $darkGray; } } } } }
	&__fullinfo {
		position: absolute;
		z-index: 15;
		top: 0;
		right: 0;
		bottom: 0;
		box-shadow: -3px -5px 4px 5px rgba(0, 0, 0, 0.2);
		transform: translate3d(-110%,0,0);
		transition: transform 0.3s ease-in-out;
		@include poynt {
			z-index: 45; }
		&.is-open {
			@include gpu; } }
	&__sidebar,
	&__container {
		position: relative; }
	&__sidebar,
	&__container,
	&__fullinfo {
		min-height: 50px;
		background: $blue;
		@include poynt {
			float: none;
			width: 100%; } }
	&__container,
	&__fullinfo {
		width: 60%; }
	&__scan {
		position: absolute;
		right: 58px;
		bottom: 0;
		display: none;
		@include poynt {
			display: block; }
		.btn-scan {
			color: $darkGray; } }

	//components
	&__menu {
		float: left;
		color: $blue;
		background: #cae9f4;
		@include active {
			background: darken(#cae9f4, 10%); }
		+ .header__title {
			padding-left: 35px; } }
	&__back-icon {
		display: inline-block;
		vertical-align: middle; }
	&__menu,
	&__back-icon {
		margin: 0 -15px 0 0;
		width: 50px;
		height: 50px;
		line-height: 50px; }
	&__title,
	&__subtitle {
		overflow: hidden;
		padding: 0 20px;
		white-space: nowrap;
		text-overflow: ellipsis; }
	&__title {
		font-size: 20px;
		margin: 0;
		&_vertical {
			padding-top: 13px;
			padding-bottom: 13px; } }
	&__subtitle {
		opacity: 0.5;
		@include rreg;
		font-size: 14px; }
	&__back {
		position: absolute;
		top: 0;
		left: 0;
		white-space: nowrap;
		.header__title {
			display: inline-block;
			vertical-align: middle; } }
	&__help,
	&__delete,
	&__search {
		position: absolute;
		top: 0;
		right: 0;
		margin: 5px;
		color: $white; }
	&__logo {
		font-size: 0;
		line-height: 150px;
		text-align: center;
		img {
			vertical-align: middle;
			max-width: 200px; }
		&_sm {
			position: relative;
			padding-top: 30px;
			margin: 0 auto;
			max-width: 450px;
			line-height: 60px;
			&:before {
				content: '';
				position: absolute;
				top: 30px;
				left: 0;
				right: 0;
				bottom: 0;
				border-radius: 2px 2px 0 0;
				background: rgba($white, 0.2); }
			img {
				max-height: 40px; } } }
	&__close {
		font-size: 16px; }
	&__toolbar {
		padding: 0 50px;
		.toolbar {
			&__col {
				border: none; } } }
	&__searchbox {
		// max-width: 300px
		max-width: 400px;
		&_right {
			position: absolute;
			top: 0;
			right: 50px;
			width: 30%;
			max-width: none;
			@include poynt {
				width: 260px; } } }
	&__sync {
		position: absolute;
		z-index: 10;
		top: 0;
		right: 0;
		height: 50px;
		padding: 5px 13px 5px 5px;
		@include poynt {
			z-index: 50; }
		.btn-sync {
			margin: -5px; } }
	&__indicator {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		width: 8px;
		background: $red;
		transition: background 0.15s ease-in-out;
		&[data-state="active"] {
			background: $green; } }
	&__search-for {
		padding: 0 0 0 10px;
		margin: -5px -5px -5px 0;
		height: 50px;
		color: inherit;
		background: none;
		@include rlight;
		font-size: 16px;
		@include poynt {
			font-size: 14px; }
		@include active {
			color: inherit;
			background: rgba(0, 0, 0, 0.1);
			box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2); }
		span, .icon {
			display: inline-block;
			vertical-align: middle; }
		span {
			@include poynt {
				margin-right: -5px; } } } }

